import { Component, OnInit } from '@angular/core';
import {Router} from "@angular/router";
@Component({
  selector: 'app-redirect-ver-categoria',
  templateUrl: './redirect-ver-categoria.component.html',
  styleUrls: ['./redirect-ver-categoria.component.css']
})
export class RedirectVerCategoriaComponent implements OnInit {

  constructor(private router:Router) { }

  ngOnInit() {
    this.router.navigate(["/productosadmin/categorias"]);
  }

}



