import { Component, OnInit } from '@angular/core';
import {ProductosadminService} from "../../../services/productosadmin/productosadmin.service";
import {Router} from "@angular/router";
import {CategoriasadminService} from "../../../services/categoriasadmin/categoriasadmin.service";
import {ActivatedRoute} from "@angular/router";
@Component({
  selector: 'app-obtenerproducto-admin',
  templateUrl: './obtenerproducto-admin.component.html',
  styleUrls: ['./obtenerproducto-admin.component.css']
})
export class ObtenerproductoAdminComponent implements OnInit {

objetoProducto;
productos;
arrayCategoria=[Object];
arrayProductos;
categorias;

  constructor(private http:ProductosadminService, private httpCategorias:CategoriasadminService,private router:Router) { }

  ngOnInit() {
    this.cargarProductos();
  }
  cargarProductos(){
    this.http.obtenerProductos().subscribe(productos=>{
     
     this.objetoProducto=productos;
     this.productos=this.objetoProducto.productos
     this.arrayProductos=this.productos;
    
    });
    //cargar categorias
    this.httpCategorias.obtenerCategorias().subscribe(categorias=>{
    this.categorias=categorias;
    });


  }
   
  buscarPorCategoria(nombreCategoria){
    this.arrayCategoria=[];
   for(let i=0;i<this.productos.length;i++){
     if(this.productos[i].categoria.includes(nombreCategoria)){
      this.arrayCategoria.push(this.productos[i]);
      console.log(this.arrayCategoria);
      this.productosFiltradosPorCategoria(this.arrayCategoria);
     }
   }
  }

   productosFiltradosPorCategoria(arrayObjetos){
    this.arrayProductos=[];
    this.arrayProductos=arrayObjetos;
    console.log(this.arrayProductos)
   }

   verTodos(){
     this.arrayProductos=this.productos;
   }

   verProducto(id){
     console.log(id);
      this.router.navigate(["/productosadmin/ver/"+id]);
   }

   

  
}



