import { Component, OnInit } from '@angular/core';
import {ComprasService} from "../../../services/comprasService/compras.service";
import {UsuariosService} from "../../../services/userService/usuarios.service";
import {Router} from "@angular/router";
import {PedidosService} from "../../../services/pedidosService/pedidos.service";
import {ProductosadminService} from "../../../services/productosadmin/productosadmin.service"; 
declare let paypal:any;

@Component({
  selector: 'app-confirmacion-compra',
  templateUrl: './confirmacion-compra.component.html',
  styleUrls: ['./confirmacion-compra.component.css']
})
export class ConfirmacionCompraComponent implements OnInit {



  loggedInUser:boolean=false; 
  public didPaypalScriptLoad:boolean=false;
  public loading:boolean=true;
  
  public paymentAmount:number=0;

  public paypalConfig:any={
    env:"sandbox",
    client:{
      sandbox:"AVOBBOdCp6iobaFwZYVe2vjk2C0-lOh4V_PijNbNBu8WWq7TJPF4eHGQ4NnnTnbMIJ5XwzhmBSVsPJEr"
    },
    style:{
      size:'large',
      shape:'rect'
    },
    commit:true,
    payment:(data,actions)=>{
      return actions.payment.create({
        payment:{
          transactions:[
            {amount:{total:this.paymentAmount, currency:"MXN"}}
          ]
        }
      });
    },
    onAuthorize:(data,actions)=>{
      return actions.payment.execute().then((payment)=>{
        this.guardarPedido();
      });
    }
  };

  public ngAfterViewChecked():void {
    if(!this.didPaypalScriptLoad){
      this.loadPaypalScript().then(()=>{
       paypal.Button.render(this.paypalConfig, "#btn-paypal");
       this.loading=false;
      });
    }
  }

  public loadPaypalScript():Promise<any> {
    this.didPaypalScriptLoad=true;
    return new Promise((resolve, reject)=>{
     const scriptElement=document.createElement("script");
     scriptElement.src="https://www.paypalobjects.com/api/checkout.js";
     scriptElement.onload=resolve;
     document.body.appendChild(scriptElement);
    });
  }

  constructor(private compras:ComprasService,private userHttp:UsuariosService, private router:Router, private pedidoHttp:PedidosService, private productosHttp:ProductosadminService) { }

  ngOnInit() {
    this.cargarProductos();
    this.loggedInUser=this.userHttp.loggedIn();
    if(this.loggedInUser==true){
     this.cargarInfoUsuario();
    }else{
     this.loggedInUser==false;
    }
  }

  objetoProductos={
    precioEnvio:0,
    productos:[],
    totalVenta:0
  };
  arrayProductos=[];
  cargarProductos(){
    this.objetoProductos=this.compras.obtenerObjetoConfirmacion().source._value;
    this.arrayProductos=this.objetoProductos.productos;
    this.paymentAmount=this.objetoProductos.totalVenta;
  }
  
  infoUsuario={
    id:"",
    email:""
  }
  objeroUsuario;
  usuarioById={
   _id:"",
   email:"",
   nombre:[],
   telefono:"",
   direcciones:[],
   pedidos:[]

  }
  objetoDireccion={
    nombre:"",
    apellido:"",
    email:"",
    calle:"",
    colonia:"",
    telefono:"",
    numero:"",
    ciudad:"",
    estado:""
  }

  cargarInfoUsuario(){
  this.infoUsuario=this.userHttp.obtenerDatosUsuarioLocal();
  this.userHttp.obtenerUsuarioById(this.infoUsuario.id).subscribe(usuario=>{
    this.objeroUsuario=usuario;
    this.usuarioById=this.objeroUsuario.user;
    this.objetoDireccion=this.usuarioById.direcciones[0];
   
   });
  }

  iniciarSesion(){
    this.router.navigate(["/iniciar-sesion"]);
  }


   
  guardarPedido(){
    const fechaHoy=new Date;
    const dd=fechaHoy.getDate();
    const mm=fechaHoy.getMonth()+1;
    const yyy=fechaHoy.getFullYear();

   if(this.loggedInUser==true){
    const objetoPedido={
      idCliente:this.usuarioById._id,
      cliente:this.usuarioById,
      productos:this.arrayProductos,
      direccion:this.objetoDireccion,
      total:this.paymentAmount,
      estadoPedido:"Pagado",
      fechaPedido:fechaHoy,
      envio:{
        nombreComp:"",
        numeroGuia:""
      }
    }
    
    this.crearPedidoconUsuario(objetoPedido);

   }else if(this.loggedInUser==false){
    const objetoPedido={

      cliente:null,
      productos:this.arrayProductos,
      direccion:this.objetoDireccion,
      total:this.paymentAmount,
      estadoPedido:"Pagado",
      fechaPedido:fechaHoy
    }
   
   
    this.pedidoHttp.guardarPedido(objetoPedido).subscribe(pedido=>{
     
     });
     this.pedidoHttp.datosPedidoCompletado(objetoPedido);
    this.router.navigate(["/compra-exitosa"]);

   }


   this.eliminarDeInventario(this.arrayProductos);
    
    
  }

  eliminarDeInventario(arrayId){

    let productoRecuperado;
    let existenciasIniciales;
   let existenciasNuevas;
    for(let i=0;i<arrayId.length;i++){
     if(arrayId[i].requiereInventario==true){
      existenciasIniciales=arrayId[i].existenciasIniciales;
      existenciasNuevas=existenciasIniciales-arrayId[i].cantidadCarrito;
      arrayId[i].existenciasIniciales=existenciasNuevas;
      this.productosHttp.editarProducto(arrayId[i]).subscribe(producto=>{
        console.log(producto);
      });
     }
    }
  }

  crearPedidoconUsuario(pedido){
    this.pedidoHttp.guardarPedido(pedido).subscribe(pedido=>{
     
     });
      
    this.pedidoHttp.datosPedidoCompletado(pedido);
    this.router.navigate(["/compra-exitosa"]);
  }

  guardarPedidoUsuario(pedido){
    this.usuarioById.pedidos.push(pedido);
   
    
    this.userHttp.editarUsuario(this.usuarioById).subscribe(usuario=>{
      
    });

    this.router.navigate(["/compra-exitosa"]);
  }
  
}



