import { Injectable } from '@angular/core';
import {HttpClient} from "@angular/common/http";
import {BehaviorSubject} from "rxjs/BehaviorSubject";

@Injectable()
export class PedidosService {
 domain:string="http://localhost:8080";

  constructor(private http:HttpClient) { }

 guardarPedido(pedido){
   return this.http.post(`/pedidos`,pedido);
 }

 obtenerPedidos(){
   return this.http.get(`/pedidos`);
 }
  
 obtenerPedidoEspecifico(id){
   return this.http.get(`/pedidos/`+id);
 }

 editarPedido(id,pedido){
   return this.http.put(`/pedidos/`+id,pedido);
 }
 private objetoPedido; 
 private objetoConfirmado;
  datosPedidoCompletado(pedido){
   pedido=this.objetoPedido=new BehaviorSubject(pedido);
  }

  obtenerDatosPedido(){
    return this.objetoConfirmado=this.objetoPedido.asObservable();
  }

  obtenerPedidoByUser(id){
    return this.http.get(`/pedidos/user/`+id);
  }

  obtenerPedidoPorFecha(objetoFecha){
   return this.http.post(`/pedidos/fecha`,objetoFecha);
  }




}



