import { Component, OnInit } from '@angular/core';
import {UsuariosService} from "../../../services/userService/usuarios.service";
import {Router} from "@angular/router";

@Component({
  selector: 'app-inicio-sesion',
  templateUrl: './inicio-sesion.component.html',
  styleUrls: ['./inicio-sesion.component.css']
})
export class InicioSesionComponent implements OnInit {

usuario={
  email:"",
  password:""
}

  constructor(private httpUser:UsuariosService, private router:Router) { }

  ngOnInit() {
  }
 objetoUserRec={success:true};
  submitData(){
    let objetoUser;
    this.httpUser.validarCuenta(this.usuario).subscribe(user=>{
     objetoUser=user;
     this.objetoUserRec=objetoUser;
     if(objetoUser.success==false){
     }else if(objetoUser.success==true){
      this.httpUser.storeUserData(objetoUser.token,objetoUser.user);
      this.router.navigate(["/perfil/usuario/"+objetoUser.user.id]);
     }
    });
  }

}



