import { Injectable } from '@angular/core';
import { BehaviorSubject } from "rxjs/BehaviorSubject";
@Injectable()
export class ComprasService {

  private objetoCarritos;
  objetoCompra;

  constructor() { }

  confirmacionCompra(objeto){
    objeto=this.objetoCarritos=new BehaviorSubject(objeto);
  }

  obtenerObjetoConfirmacion(){
    return this.objetoCompra= this.objetoCarritos.asObservable();
  }

}



