import { Component, OnInit } from '@angular/core';
import {Coleccion} from "../coleccion";
import {CollectionsAdminService} from "../../../../services/collectionsadmin/collections-admin.service";
import {Router} from "@angular/router";
@Component({
  selector: 'app-agregar-coleccion',
  templateUrl: './agregar-coleccion.component.html',
  styleUrls: ['./agregar-coleccion.component.css']
})
export class AgregarColeccionComponent implements OnInit {

coleccionNueva:Coleccion={
  nombre:"",
  descripcion:"",
  productos:[Object]
}
success:boolean;
returned;
  constructor(private http:CollectionsAdminService, private router:Router) { }

  ngOnInit() {
  }

  guardarColeccion(data){
    
    this.http.guardarColeccion(data.form.value).subscribe(coleccion=>{
      console.log(coleccion);
      this.returned=coleccion;
      this.success=this.returned.success;
      if(this.success==false){
        console.log("error al crear");
      }else{
      this.router.navigate(["/coleccionesadmin"]);
      }
      

    });
  }

}



