import { Component, OnInit } from '@angular/core';
import {Router} from "@angular/router";
@Component({
  selector: 'app-redirect-productos-admin',
  templateUrl: './redirect-productos-admin.component.html',
  styleUrls: ['./redirect-productos-admin.component.css']
})
export class RedirectProductosAdminComponent implements OnInit {

  constructor(private router:Router) { }

  ngOnInit() {
    this.router.navigate(["/productosadmin/agregar"]);
  }

}



