import { Component, OnInit, TemplateRef } from '@angular/core';
import {Coleccion} from "../coleccion";
import {CollectionsAdminService} from "../../../../services/collectionsadmin/collections-admin.service";
import {ProductosadminService} from "../../../../services/productosadmin/productosadmin.service";
import {ActivatedRoute} from "@angular/router";
import {ControlInterfazService} from "../../../../services/controlInterfaz/control-interfaz.service";
import {BsModalService} from 'ngx-bootstrap/modal';
import {BsModalRef} from 'ngx-bootstrap/modal/bs-modal-ref.service';
import {AlertsService} from "../../../../services/alertsService/alerts.service";
import {Router} from "@angular/router";
@Component({
  selector: 'app-coleccion-especifica',
  templateUrl: './coleccion-especifica.component.html',
  styleUrls: ['./coleccion-especifica.component.css']
})
export class ColeccionEspecificaComponent implements OnInit {
///modal

modalRef:BsModalRef;
mensaje:string;




idColeccion;
coleccionNueva:Coleccion={
  nombre:"",
  descripcion:"",
  productos:[]
}

objetoRegresado;
coleccion;
nombreProducto:string="";

  constructor(private http:CollectionsAdminService,private route:ActivatedRoute,private httpProductos:ProductosadminService, private httpInterfaz:ControlInterfazService,
   private modalService:BsModalService, private alertService:AlertsService, private router:Router) {
    this.route.params.subscribe(params=>{
          this.idColeccion=params.id;
    });
   }

   openModal(template:TemplateRef<any>){
     this.modalRef=this.modalService.show(template,{class:'modal-sm'});
   }

   confirmarModal(){
     let objetoRecuperado;
     let coleccionRecuperada;
    this.modalRef.hide();
    this.http.eliminarColeccion(this.idColeccion).subscribe(coleccion=>{
       objetoRecuperado=coleccion;
       coleccionRecuperada=objetoRecuperado;
       if(coleccionRecuperada.success==true){
        const alertObj={
          type:'danger',
          msg:"coleccion eliminada",
          timeout:3000
        };
         this.alertService.agregarAlerta(alertObj);
         this.router.navigate(['coleccionesadmin/editar/'+this.idColeccion]);
       }else{
        const alertObj={
          type:'danger',
          msg:"error al eliminar coleccion",
          timeout:3000
        };
         this.alertService.agregarAlerta(alertObj);
         this.router.navigate(['coleccionesadmin/editar/'+this.idColeccion]);
       }
    });
   }
   cancelarModal(){
     this.modalRef.hide();
   }

   alerts=[];
  ngOnInit() {
    this.recuperarColeccion();
    this.alerts=this.alertService.obtenerAlerta();
    console.log('====================================');
    console.log(this.alerts);
    console.log('====================================');
    this.alertService.resetArray();
  }

  recuperarColeccion(){
   this.http.obtenerColeccionEspecifica(this.idColeccion).subscribe(coleccion=>{
      this.objetoRegresado=coleccion;
       this.coleccion=this.objetoRegresado.coleccion;
       this.coleccionNueva.nombre=this.coleccion.nombre;
       this.coleccionNueva.descripcion=this.coleccion.descripcion;
       this.coleccionNueva.productos=this.coleccion.productos;
   });
  }


  objetoProductosRecuperado;
  productosRecuperados;
  buscarProducto(){
    console.log(this.nombreProducto );
    let objeto={
      nombreProducto:this.nombreProducto
    }
    this.httpProductos.buscarProducto(objeto).subscribe(producto=>{
     this.objetoProductosRecuperado=producto;
     this.productosRecuperados=this.objetoProductosRecuperado.productos;
     
    });
  }

  isFocused=false;
  metodoFocus(){
    this.buscarProducto()
   this.isFocused=true;
   
  }

  fueraDeFocus(){
    this.isFocused=false;
  }
  

    agregarProductoAColeccion(producto){
      this.coleccionNueva.productos.push(producto);
      console.log(this.coleccionNueva.productos);
    }

   

    eliminarProductoDeColeccion(id){
    let lugar;
    
    for(let i=0;i<this.coleccionNueva.productos.length;i++){
      if(id==this.coleccionNueva.productos[i]._id){
 let index = this.coleccionNueva.productos.indexOf(this.coleccionNueva.productos[i]);
      lugar=index;
      console.log(lugar);
      this.coleccionNueva.productos.splice(lugar,1);
      }

     
     
    }
   

    }

    
    objetoInterfazRecuperada;
    interfazRecuperada;
   
     coleccionSeleccionada;
    buscarColeccion(arrayColecciones){
      for(let i=0;i<arrayColecciones.length;i++){
        if(arrayColecciones[i]._id.includes(this.idColeccion)){
          
         this.coleccionSeleccionada=arrayColecciones[i]
        }else{
          this.coleccionSeleccionada=this.coleccionNueva;
        }
      }
    }



    guardarCambios(){      
 

      let objetoRecuperado={}

      let coleccionRecuperada;

      this.http.editarColeccion(this.idColeccion,this.coleccionNueva).subscribe(coleccion=>{
         objetoRecuperado=coleccion;
         coleccionRecuperada=objetoRecuperado;
         
         if(coleccionRecuperada.success==true){ 
          const alertObj={
            type:'success',
            msg:"coleccion editada",
            timeout:3000
          };
            this.alertService.agregarAlerta(alertObj);
             this.router.navigate(['coleccionesadmin/editar/'+this.idColeccion]);
         }else{
          const alertObj={
            type:'success',
            msg:"Error al editar coleccion",
            timeout:3000
          };
          this.alertService.agregarAlerta(alertObj);
          this.router.navigate(['coleccionesadmin/editar/'+this.idColeccion]);
         }
      });

    }
}



