import { Component, OnInit } from '@angular/core';
import {CollectionsAdminService} from "../../../services/collectionsadmin/collections-admin.service";
@Component({
  selector: 'app-colecciones',
  templateUrl: './colecciones.component.html',
  styleUrls: ['./colecciones.component.css']
})
export class ColeccionesComponent implements OnInit {

ObjetoRegresado;
colecciones;

  constructor(private http:CollectionsAdminService) { }

  ngOnInit() {
    this.obtenerColecciones();
  }

  obtenerColecciones(){
    this.http.obtenerColecciones().subscribe(colecciones=>{
    this.ObjetoRegresado=colecciones;
     this.colecciones=this.ObjetoRegresado.colecciones;
     console.log(this.colecciones);
    });
  }

}



