import { Component, OnInit } from '@angular/core';
import {ActivatedRoute} from "@angular/router";
import {CategoriasadminService} from "../../../services/categoriasadmin/categoriasadmin.service";
@Component({
  selector: 'app-ver-categoria',
  templateUrl: './ver-categoria.component.html',
  styleUrls: ['./ver-categoria.component.css']
})
export class VerCategoriaComponent implements OnInit {

  constructor(private route:ActivatedRoute, private categoriasHttp:CategoriasadminService) { }
  
  idColeccion;
  ngOnInit() {
    this.route.params.subscribe(params=>{
      this.idColeccion=params.id;
    });
    this.obtenerColeccionEspecifica(this.idColeccion);
  }

  obtenerColeccionEspecifica(id){
this.categoriasHttp.obtenerCategoriaEspecifica(id).subscribe(categoria=>{
   console.log(categoria);
 });
  }

}



