import { Injectable } from '@angular/core';
import {Router,CanActivate} from "@angular/router";
import {UsuariosService} from "../services/userService/usuarios.service";
@Injectable()
export class GuardUser implements CanActivate{

  constructor(private http:UsuariosService, private router:Router) { }
  
  canActivate(){
     if(this.http.loggedIn()){
        return true;
     } else{
       this.router.navigate(["/iniciar-sesion"]);
       return false;
     }
  }
}



