import { Component, OnInit } from '@angular/core';
import {PedidosService} from "../../services/pedidosService/pedidos.service";
import {Chart} from "chart.js";
@Component({
  selector: 'app-ventas-admin',
  templateUrl: './ventas-admin.component.html',
  styleUrls: ['./ventas-admin.component.css']
})
export class VentasAdminComponent implements OnInit {

  constructor(private pedidosHttp:PedidosService) { }

  grafica=[];

  ngOnInit() {
    this.obtenerPedidos();
    
  }

  objetoPedido;
  pedidoRecuperados
  obtenerPedidos(){
    this.pedidosHttp.obtenerPedidos().subscribe(pedido=>{
    let fechaPedidos =[];
    let ventaPedido =[];
     this.objetoPedido=pedido;
     this.pedidoRecuperados=this.objetoPedido.pedidos;
     this.pedidoRecuperados.forEach(res => {
       let jsDate=new Date(res.fechaPedido);
         fechaPedidos.push(jsDate.toLocaleTimeString('en',{year:'numeric',month:'short',day:'numeric'}));
         ventaPedido.push(res.total);
      
      });
        
      this.grafica=new Chart('canvas',{
        type:'bar',
        data:{
         labels:fechaPedidos,
         datasets:[{
           label:"Ventas",
           data:ventaPedido,
           backgroundColor: [
            'rgba(255, 99, 132, 0.3)',
            'rgba(54, 162, 235, 0.3)',
            'rgba(255, 206, 86, 0.3)',
            'rgba(75, 192, 192, 0.3)',
            'rgba(153, 102, 255, 0.3)',
            'rgba(255, 159, 64, 0.3)'
        ],
        borderColor: [
            'rgba(255,99,132,1)',
            'rgba(54, 162, 235, 1)',
            'rgba(255, 206, 86, 1)',
            'rgba(75, 192, 192, 1)',
            'rgba(153, 102, 255, 1)',
            'rgba(255, 159, 64, 1)'
        ],
        borderWidth: 1
         }]
        },
        options: {
          scales: {
              yAxes: [{
                  ticks: {
                      beginAtZero:true
                  }
              }]
          }
      }
      });
      this.filtrarPedidosDiarios(this.pedidoRecuperados);
    });
  }

  arrayVentasHoy=[];

  filtrarPedidosDiarios(arrayPedidos){
    
      let fechaHoy=new Date;
      let fechaHoyFormat=fechaHoy.toLocaleTimeString('en',{year:'numeric',month:'short',day:'numeric'});
      console.log(arrayPedidos);
      

    
      arrayPedidos.forEach(pedidos => {
         let fechas=new Date(pedidos.fechaPedido);
      
         let dd=fechas.getDate();
         let mm=fechas.getMonth()+1;
         let yy=fechas.getFullYear(); 


         let dh=fechaHoy.getDate();
         let mh=fechaHoy.getMonth()+1;
         let yh=fechaHoy.getFullYear();

         if(dd==dh && mm==mh && yy==yh){
           this.arrayVentasHoy.push(pedidos);
         }
         console.log(this.arrayVentasHoy);
      });
      
  }

 

}



