import { Injectable } from '@angular/core';
import {Admin} from "../components/register-admin/admin";
import {HttpClient, HttpHeaders} from "@angular/common/http";
import {Headers} from "@angular/http";
import {tokenNotExpired} from "angular2-jwt";


@Injectable()
export class HttpAdminService {
domain:String="http://localhost:8080"
authToken;
user;

  constructor(public http:HttpClient) { }

  validarCuenta(cuenta){
   return this.http.post(`/admin/login`,cuenta);
  }

  storeUserData(token,user){
   localStorage.setItem("id_token",token);
   localStorage.setItem("admin",JSON.stringify(user));
   this.authToken=token;
   this.user=user;
  }
  cargarToken(){
    const token=localStorage.getItem("id_token");
    this.authToken=token;
  }
  
  loggedIn(){
    return tokenNotExpired("id_token");
  }

  getAdmin(){
    let head=new HttpHeaders({"Authorization":this.authToken});
    this.cargarToken();
    return this.http.get(`/admin`,{headers:{"Authorization ":this.authToken}});
  }

  cerrarSesion(){
    this.authToken=null;
    this.user=null;
    localStorage.clear();
    console.log("session closed");
  }

}



