import { Component, OnInit } from '@angular/core';
import {UsuariosService} from "../../../services/userService/usuarios.service";
import {Router} from "@angular/router";
@Component({
  selector: 'app-registro-usuario',
  templateUrl: './registro-usuario.component.html',
  styleUrls: ['./registro-usuario.component.css']
})
export class RegistroUsuarioComponent implements OnInit {
  nombreUsuario;
  apellidoUsuario;
 
  nuevoUsuario={
    nombre:[],
    direccion:[""],
    password:"",
    email:"",
    pedidos:[{}],
    telefono:""
  }

  success:boolean;
  notSuccess:boolean;

  constructor(private httpUsuarios:UsuariosService) { }

  ngOnInit() {

  }
 

  crearUsuario(data){
    this.nuevoUsuario.nombre=[];
  let objetoRecuperado;
  let objetoUsuario;
    this.nuevoUsuario.nombre.push(this.nombreUsuario,this.apellidoUsuario);

    this.httpUsuarios.guardarUsuario(this.nuevoUsuario).subscribe(usuario=>{
      objetoRecuperado=usuario;
      objetoUsuario=objetoRecuperado.created;

      this.success=objetoRecuperado.created;
       console.log(this.success);
     console.log(usuario);
    });
  }





}



