import { Component, OnInit } from '@angular/core';
import {ControlInterfazService} from "../../../services/controlInterfaz/control-interfaz.service"
@Component({
  selector: 'app-carousel-index',
  templateUrl: './carousel-index.component.html',
  styleUrls: ['./carousel-index.component.css']
})
export class CarouselIndexComponent implements OnInit {

  constructor(private httpInterfaz:ControlInterfazService) { }

  ngOnInit() {
    this.cargarInterfaz();
  }
  objetoInterfaz;
  imagenesCarousel=["","",""];
  cargarInterfaz(){
    this.httpInterfaz.obtenerInterfaz().subscribe(interfaz=>{
      this.objetoInterfaz=interfaz;
      this.imagenesCarousel=this.objetoInterfaz.interfaz[0].imagenesCarousel;
    });
  }

}



