import { Component, OnInit } from '@angular/core';
import {PedidosService} from '../../../services/pedidosService/pedidos.service';
@Component({
  selector: 'app-ventas-historicas',
  templateUrl: './ventas-historicas.component.html',
  styleUrls: ['./ventas-historicas.component.css']
})
export class VentasHistoricasComponent implements OnInit {

  constructor(private pedidosHttp:PedidosService) { }

  ngOnInit() {
    this.obtenerPedidos();
  }
  
  objetoPedidos;
  pedidos;
  obtenerPedidos(){
    this.pedidosHttp.obtenerPedidos().subscribe(pedidos=>{
      this.objetoPedidos=pedidos;
      this.pedidos=this.objetoPedidos.pedidos;
    });
  }



}



