import { Component, OnInit } from '@angular/core';
import {Router} from "@angular/router";
import { ActivatedRoute } from '@angular/router';
@Component({
  selector: 'app-redirect-producto',
  templateUrl: './redirect-producto.component.html',
  styleUrls: ['./redirect-producto.component.css']
})
export class RedirectProductoComponent implements OnInit {

  constructor(private router:Router, private route:ActivatedRoute) { }
 idProducto;
  ngOnInit() {
    this.route.params.subscribe(params=>{
      this.idProducto=params.id;
    });
    this.router.navigate(["/producto/"+this.idProducto]);
  }

}



