import { Component, OnInit } from '@angular/core';
import {Router} from "@angular/router";
import {ActivatedRoute} from "@angular/router";
@Component({
  selector: 'app-redirect-direcciones',
  templateUrl: './redirect-direcciones.component.html',
  styleUrls: ['./redirect-direcciones.component.css']
})
export class RedirectDireccionesComponent implements OnInit {

  constructor(private router:Router, private route:ActivatedRoute) { }

  idUser;
  ngOnInit() {
    this.route.params.subscribe(params=>{
     this.idUser=params.id;
    });
    this.router.navigate(["/perfil/"+this.idUser+"/direcciones"]);
  }

}



