import { Injectable } from '@angular/core';
import {HttpClient} from "@angular/common/http";
import {Categoria} from "../../components/categorias-admin/categoria";

@Injectable()
export class CategoriasadminService {

  domain:string="http://localhost:8080";

  constructor(private http:HttpClient) { }

  guardarCategoria(categoria:Categoria){
    return this.http.post(`/categorias`,categoria);
  }
  obtenerCategorias(){
    return this.http.get(`/categorias`);
  }
  editarCategorias(id,categoria){
    return this.http.put(`/categorias/`+id,categoria)
  }
  obtenerCategoriaEspecifica(id){
    return this.http.get(`/categorias/`+id);
  }
  eliminarCategoria(id){
    return this.http.delete(`/categorias/`+id);
  }

}



