import { Injectable } from '@angular/core';
import {BehaviorSubject} from "rxjs/BehaviorSubject";
import {Producto} from "../../components/productos-admin/agregarproducto-admin/Productos";
import "rxjs/add/observable/of";
import { Observable } from 'rxjs/Observable';
import { Subject } from 'rxjs/Subject';
import {HttpClient} from "@angular/common/http";
@Injectable()
export class CarritoComprasService {
  arrayProductos:Producto[]=[];
private producto= new Subject<Producto>();
cast = Observable.of(this.arrayProductos);

  constructor(private httpClient:HttpClient) {
    if(this.arrayProductos.length<1){
      localStorage.setItem("cart_productos",JSON.stringify(this.arrayProductos));
      console.log(this.arrayProductos.length)
    }
   }

   ngOnInit() {
     
   }
   
  agregarProducto(nuevoProducto){
    this.arrayProductos=JSON.parse(localStorage.getItem("cart_productos"));
    this.arrayProductos.push(nuevoProducto);
    this.producto.next(<Producto>{});
    localStorage.setItem("cart_productos",JSON.stringify(this.arrayProductos));
    
  }
  
  obtenerProductos(){
    return this.arrayProductos=JSON.parse(localStorage.getItem("cart_productos"));
  }
  
  eliminarProducto(arrayProductos){
   this.arrayProductos=arrayProductos;
   localStorage.setItem("cart_productos",JSON.stringify(this.arrayProductos));
  }
  

}



