import { Component, OnInit } from '@angular/core';
import {CategoriasadminService} from "../../services/categoriasadmin/categoriasadmin.service"
import {Router} from "@angular/router";
@Component({
  selector: 'app-categorias-admin',
  templateUrl: './categorias-admin.component.html',
  styleUrls: ['./categorias-admin.component.css']
})
export class CategoriasAdminComponent implements OnInit {

  constructor(private categoriasHttp:CategoriasadminService, private router:Router) { }

  ngOnInit() {
    this.obtenerCategorias();
  }

  objetoRecuperado;
  categoriasRecuperadas;
  obtenerCategorias(){
    this.categoriasHttp.obtenerCategorias().subscribe(categorias=>{
    this.categoriasRecuperadas=categorias;
    console.log(categorias);
    });
  }

  verCategoria(id){
      this.router.navigate(["/productosadmin/categorias/ver/"+id]);
  }

  eliminarCategoria(id){
    this.categoriasHttp.eliminarCategoria(id).subscribe(categoria=>{
       console.log(categoria);
    });
    this.router.navigate(["/productosadmin/categorias/ver/redirect"]);
  }

}



