import { Component, OnInit } from '@angular/core';
import {Router} from "@angular/router";
@Component({
  selector: 'app-redirect-carrito',
  templateUrl: './redirect-carrito.component.html',
  styleUrls: ['./redirect-carrito.component.css']
})
export class RedirectCarritoComponent implements OnInit {

  constructor(private router:Router) { }

  ngOnInit() {
    this.router.navigate(["/carrito"]);
  }

}



