import { Component, OnInit } from '@angular/core';
import {CarritoComprasService} from "../../../services/carritoService/carrito-compras.service";
import {Router} from "@angular/router";
import {ComprasService} from "../../../services/comprasService/compras.service";

@Component({
  selector: 'app-carrito-compras',
  templateUrl: './carrito-compras.component.html',
  styleUrls: ['./carrito-compras.component.css']
})
export class CarritoComprasComponent implements OnInit {

  constructor(private carritoService:CarritoComprasService, private router:Router, private compras:ComprasService) { }
productos=[];
  ngOnInit() {
 this.productos=this.carritoService.obtenerProductos();
 console.log(this.productos);
  this.obtenerTotal(this.productos)
  }

  multiTotal:number;
  sumaTotal:number=0;
  precioEnvio:number=150;
  obtenerTotal(array){
    this.sumaTotal=0;
    if(array.length>0){
      for(let i=0;i<array.length;i++){
      
        this.multiTotal=array[i].cantidadCarrito*array[i].precioVenta;
        console.log(this.multiTotal);
        this.sumaTotal+=this.multiTotal;
       } 
       this.sumaTotal+=this.precioEnvio;
    }
   
  }

  eliminarProducto(producto){
    for(let i=0; i<this.productos.length; i++){
      if(this.productos[i]._id==producto._id){
        console.log(this.productos.indexOf(this.productos[i]));
        this.productos.splice(this.productos.indexOf(this.productos[i]),1);
      }
    }
    this.carritoService.eliminarProducto(this.productos);
    this.router.navigate(["/carrito/delete"]);
  }

  objetoCompras={
    productos:[],
    precioEnvio:0,
    totalVenta:0
  }
  confirmacionCompra(){

    this.objetoCompras={
      productos:this.productos,
      precioEnvio:this.precioEnvio,
      totalVenta:this.sumaTotal
    }
  

    this.compras.confirmacionCompra(this.objetoCompras);
    this.router.navigate(["/carrito/confirmacion"]);
    
  }

 
}



