import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import {Routes,RouterModule} from "@angular/router";
import {FormsModule, ReactiveFormsModule} from "@angular/forms";
import {HttpClientModule} from "@angular/common/http";
import {HttpModule} from "@angular/http";
import {ModalModule} from "ngx-bootstrap";

import { AppComponent } from './app.component';
import { NavbarComponent } from './components/navbar-admin/navbar.component';
import { LoginAdminComponent } from './components/login-admin/login-admin.component';
import { InicioAdminComponent } from './components/inicio-admin/inicio-admin.component';
import { RegisterAdminComponent } from './components/register-admin/register-admin.component';


//services
import {HttpAdminService} from "./services/http-admin.service";
import {ProductosadminService} from "./services/productosadmin/productosadmin.service";
import {CategoriasadminService} from "./services/categoriasadmin/categoriasadmin.service";
import {CollectionsAdminService} from "./services/collectionsadmin/collections-admin.service";
import {ControlInterfazService} from "./services/controlInterfaz/control-interfaz.service";
import {NavbarService} from "./services/controlInterfaz/navbarService/navbar-service.service";
import {CarritoComprasService} from "../app/services/carritoService/carrito-compras.service";
import {ComprasService} from "../app/services/comprasService/compras.service";
import {PedidosService} from "../app/services/pedidosService/pedidos.service";
import {AlertsService} from "./services/alertsService/alerts.service";

//guard
import {GuardAdminService} from "./guards/guard-admin.service";
import {GuardUser} from "./guards/guard-user.service";


import { ProductosadmiComponent } from './components/productos-admin/productosadmi.component';
import { AgregarproductoAdminComponent } from './components/productos-admin/agregarproducto-admin/agregarproducto-admin.component';
import { SidebarproductosComponent } from './components/productos-admin/sidebar-productosadmin/sidebarproductos/sidebarproductos.component';
import { ObtenerproductoAdminComponent } from './components/productos-admin/obtenerproducto-admin/obtenerproducto-admin.component';
import { CategoriasAdminComponent } from './components/categorias-admin/categorias-admin.component';
import { AgregarCategoriaComponent } from './components/categorias-admin/agregar-categoria/agregar-categoria.component';
import { VerproductoEspecificoAdminComponent } from './components/productos-admin/verproducto-especifico-admin/verproducto-especifico-admin.component';
import { IndexComponent } from './components/frontEnd/index/index.component';
import { NavbarIndexComponent } from './components/frontEnd/navbar-index/navbar-index.component';
import { CarouselIndexComponent } from './components/frontEnd/carousel-index/carousel-index.component';
import { DestacadosIndexComponent } from './components/frontEnd/destacados-index/destacados-index.component';
import { ColeccionesComponent } from './components/productos-admin/colecciones/colecciones.component';
import { AgregarColeccionComponent } from './components/productos-admin/colecciones/agregar-coleccion/agregar-coleccion.component';
import { ColeccionEspecificaComponent } from './components/productos-admin/colecciones/coleccion-especifica/coleccion-especifica.component';
import { ControlInterfazComponent } from './components/control-interfaz/control-interfaz.component';
import { ColeccionEspecificaFrontComponent } from './components/frontEnd/coleccion-especifica-front/coleccion-especifica-front.component';
import { RedirectColeccionEspecificaComponent } from './components/frontEnd/redirect-coleccion-especifica/redirect-coleccion-especifica.component';
import { InicioSesionComponent } from './components/frontEnd/inicio-sesion/inicio-sesion.component';
import { RegistroUsuarioComponent } from './components/frontEnd/registro-usuario/registro-usuario.component';
import {UsuariosService} from "./services/userService/usuarios.service";
import { PerfilUsuarioComponent } from './components/frontEnd/perfil-usuario/perfil-usuario.component';
import { CarritoComprasComponent } from './components/frontEnd/carrito-compras/carrito-compras.component';
import { RedirectDestacadosComponent } from './components/frontEnd/redirects-carrito/redirect-destacados/redirect-destacados.component';
import { ProductoEspecificoComponent } from './components/frontEnd/producto-especifico/producto-especifico.component';
import { RedirectProductoComponent } from './components/frontEnd/producto-especifico/redirect-producto/redirect-producto.component';
import {RedirectColeccionAdmin} from './components/productos-admin/colecciones/redirectColeccion';
/////////////PIPES
import {precioMultiplicacionPipe} from "./components/frontEnd/carrito-compras/pipePrecio";
import { RedirectCarritoComponent } from './components/frontEnd/carrito-compras/redirect-carrito/redirect-carrito.component';
import { ConfirmacionCompraComponent } from './components/frontEnd/confirmacion-compra/confirmacion-compra.component';
import { DireccionesComponent } from './components/frontEnd/perfil-usuario/direcciones/direcciones.component';
import { RedirectDireccionesComponent } from './components/frontEnd/perfil-usuario/direcciones/redirect-direcciones/redirect-direcciones.component';
import { CompraExitosaComponent } from './components/frontEnd/compra-exitosa/compra-exitosa.component';
import { PedidoEspecificoComponent } from './components/frontEnd/perfil-usuario/pedido-especifico/pedido-especifico.component';
import { FooterComponent } from './components/frontEnd/footer/footer.component';

import { AlertModule } from 'ngx-bootstrap';
import { RedirectProductosAdminComponent } from './components/productos-admin/redirect-productos-admin/redirect-productos-admin.component';
import { VentasAdminComponent } from './components/ventas-admin/ventas-admin.component';
import { NavbarVentasComponent } from './components/ventas-admin/navbar-ventas/navbar-ventas.component';
import { VentasHistoricasComponent } from './components/ventas-admin/ventas-historicas/ventas-historicas.component';
import { VentaEspecificaComponent } from './components/ventas-admin/venta-especifica/venta-especifica.component';
import { VerCategoriaComponent } from './components/categorias-admin/ver-categoria/ver-categoria.component';
import { RedirectVerCategoriaComponent } from './components/categorias-admin/redirect-ver-categoria/redirect-ver-categoria.component';
import { EditarPerfilComponent } from './components/frontEnd/perfil-usuario/editar-perfil/editar-perfil.component';


const appRoutes:Routes=[
  {path:"login/admin",component:LoginAdminComponent},
  {path:"admin",component:InicioAdminComponent,canActivate:[GuardAdminService]},
  {path:"admin/ventas",component:VentasAdminComponent,canActivate:[GuardAdminService]},
  {path:"admin/ventas/all",component:VentasHistoricasComponent,canActivate:[GuardAdminService]},
  {path:"admin/ventas/:id",component:VentaEspecificaComponent,canActivate:[GuardAdminService]},
  {path:"productosadmin",component:ProductosadmiComponent,canActivate:[GuardAdminService]},
  {path:"productosadmin/agregar",component:AgregarproductoAdminComponent,
   canActivate:[GuardAdminService]},
   {path:"productosadmin/ver",component:ObtenerproductoAdminComponent,canActivate:[GuardAdminService]},
   {path:"productosadmin/ver/:id",component:VerproductoEspecificoAdminComponent,canActivate:[GuardAdminService]},
   {path:"productosadmin/agregar/redirect",component:RedirectProductosAdminComponent, canActivate:[GuardAdminService]},
   {path:"productosadmin/categorias", component:CategoriasAdminComponent, canActivate:[GuardAdminService]},
{path:"productosadmin/categorias/agregar", component:AgregarCategoriaComponent, canActivate:[GuardAdminService]},
{path:"productosadmin/categorias/ver/redirect",component:RedirectVerCategoriaComponent,canActivate:[GuardAdminService]},
{path:"productosadmin/categorias/ver/:id",component:VerCategoriaComponent, canActivate:[GuardAdminService]},
{path:"coleccionesadmin", component:ColeccionesComponent, canActivate:[GuardAdminService]},
{path:"coleccionesadmin/agregar", component:AgregarColeccionComponent, canActivate:[GuardAdminService]},
{path:"coleccionesadmin/ver/:id", component:ColeccionEspecificaComponent,canActivate:[GuardAdminService]},
{path:"coleccionesadmin/editar/:id",component:RedirectColeccionAdmin,canActivate:[GuardAdminService]},
  ///////////Rutas Edicion Interfaz/////////////////////////////
  {path:"uiadmin",component:ControlInterfazComponent,canActivate:[GuardAdminService]},
  
 ////////////RUTAS FRONT END //////////////////////////////////
{path:"", component:IndexComponent},
{path:"colecciones/redirect/:id",component:RedirectColeccionEspecificaComponent},
{path:"colecciones/front/:id",component:ColeccionEspecificaFrontComponent},


/////////////////INICIO SESION FRONT///////////
{path:"iniciar-sesion",component:InicioSesionComponent},
{path:"registrar", component:RegistroUsuarioComponent},
{path:"perfil/usuario/:id", component:PerfilUsuarioComponent,canActivate:[GuardUser]},
{path:"perfil/:id/direcciones", component:DireccionesComponent,canActivate:[GuardUser]},
{path:"redirect/direccion/user/:id", component:RedirectDireccionesComponent, canActivate:[GuardUser]},
{path:"perfil/pedido/:id",component:PedidoEspecificoComponent,canActivate:[GuardUser]},

//////////////////CARRITOOCMPRAS////////////////
{path:"carrito",component:CarritoComprasComponent},
{path:"carrito/confirmacion",component:ConfirmacionCompraComponent},
{path:"compra-exitosa",component:CompraExitosaComponent},

////////////////redirect carrito/////////////////
{path:"carrito/destacados/add",component:RedirectDestacadosComponent},
{path:"carrito/coleccion/:id",component:RedirectColeccionEspecificaComponent},
{path:"carrito/producto/:id", component:RedirectProductoComponent},
{path:"carrito/delete",component:RedirectCarritoComponent},

////producto especifico///////////////////////////
{path:"producto/:id",component:ProductoEspecificoComponent}
]



@NgModule({
  declarations: [
    AppComponent,
    NavbarComponent,
    LoginAdminComponent,
    InicioAdminComponent,
    RegisterAdminComponent,
    ProductosadmiComponent,
    AgregarproductoAdminComponent,
    SidebarproductosComponent,
    ObtenerproductoAdminComponent,
    CategoriasAdminComponent,
    AgregarCategoriaComponent,
    VerproductoEspecificoAdminComponent,
    IndexComponent,
    NavbarIndexComponent,
    CarouselIndexComponent,
    DestacadosIndexComponent,
    ColeccionesComponent,
    AgregarColeccionComponent,
    ColeccionEspecificaComponent,
    ControlInterfazComponent,
    ColeccionEspecificaFrontComponent,
    RedirectColeccionEspecificaComponent,
    InicioSesionComponent,
    RegistroUsuarioComponent,
    PerfilUsuarioComponent,
    CarritoComprasComponent,
    RedirectDestacadosComponent,
    ProductoEspecificoComponent,
    RedirectProductoComponent,
    precioMultiplicacionPipe,
    RedirectCarritoComponent,
    ConfirmacionCompraComponent,
    DireccionesComponent,
    RedirectDireccionesComponent,
    CompraExitosaComponent,
    PedidoEspecificoComponent,
    FooterComponent,
    RedirectProductosAdminComponent,
    VentasAdminComponent,
    NavbarVentasComponent,
    VentasHistoricasComponent,
    VentaEspecificaComponent,
    VerCategoriaComponent,
    RedirectVerCategoriaComponent,
    EditarPerfilComponent,
    RedirectColeccionAdmin
  ],
  imports: [
    BrowserModule,
    RouterModule.forRoot(appRoutes),
    FormsModule,
    ReactiveFormsModule,
    HttpClientModule,
    AlertModule.forRoot(),
    ModalModule.forRoot()
  ],
  providers: [HttpAdminService,GuardAdminService,ProductosadminService,CategoriasadminService,CollectionsAdminService,ControlInterfazService,NavbarService,UsuariosService,GuardUser,CarritoComprasService,ComprasService,PedidosService,AlertsService],
  bootstrap: [AppComponent]
})
export class AppModule { }



