import { Component, OnInit } from '@angular/core';
import {Categoria} from "../categoria";
import {CategoriasadminService} from "../../../services/categoriasadmin/categoriasadmin.service"
@Component({
  selector: 'app-agregar-categoria',
  templateUrl: './agregar-categoria.component.html',
  styleUrls: ['./agregar-categoria.component.css']
})
export class AgregarCategoriaComponent implements OnInit {

  nuevaCategoria:Categoria={
    nombre:"",
    descripcion:""
  }

  constructor(private http:CategoriasadminService) { }

  ngOnInit() {
  }

  guardarCategoria(datos){
    console.log(datos.form.value);
    this.http.guardarCategoria(datos.form.value).subscribe(categoria=>{
     console.log(categoria);
    });
  }

}



