import { Component, OnInit } from '@angular/core';
import {PedidosService} from "../../../services/pedidosService/pedidos.service";
@Component({
  selector: 'app-compra-exitosa',
  templateUrl: './compra-exitosa.component.html',
  styleUrls: ['./compra-exitosa.component.css']
})
export class CompraExitosaComponent implements OnInit {

  constructor(private pedidosHttp:PedidosService) { }

  ngOnInit() {
    this.obtenerDatosPedido();
  }
 
  objetoPedido:{};
  objetoPedidoRecuperado;
  obtenerDatosPedido(){
    this.pedidosHttp.obtenerDatosPedido().subscribe(pedido=>{
      
       this.objetoPedido=pedido;
       this.objetoPedidoRecuperado=this.objetoPedido;
       
    });
  }

}



