import { Injectable } from '@angular/core';
import {HttpClient, HttpHeaders} from "@angular/common/http";
import {Headers} from "@angular/http";
import { tokenNotExpired } from 'angular2-jwt';
@Injectable()
export class UsuariosService {

  domain:string="http://localhost:8080"
  authToken;
  user;
  constructor(private http:HttpClient) { }
  
  validarCuenta(cuenta){
    return this.http.post(`/usuario/login`,cuenta);
  }

  storeUserData(token,user){
    localStorage.setItem("token_id",token);
    localStorage.setItem("usuario",JSON.stringify(user));
    this.authToken=token;
    this.user=user;
  }

  cargarToken(){
    const token=localStorage.getItem("token_id");
    this.authToken=token;
  }

  loggedIn(){
    return tokenNotExpired("token_id");
  }

  getUser(){
   let head=new HttpHeaders({"Authorization":this.authToken});
   this.cargarToken();
   return this.http.get(`/user`,{headers:{"Authorization":this.authToken}});
  }

  cerrarSesion(){
    this.authToken=null;
    this.user=null;
    localStorage.removeItem("token_id");
    localStorage.removeItem("usuario");

  }
  
  guardarUsuario(usuario){
   return this.http.post(`/signup`,usuario);
  }

  obtenerDatosUsuarioLocal(){
   return JSON.parse(localStorage.getItem("usuario"));
  }

  obtenerUsuarioById(id){
    return this.http.get(`/usuario/loggedIn/`+id);
  }
  editarUsuario(usuario){
    let id=usuario.id;
    console.log(usuario);
    return this.http.post(`/usuario/editar/`+id,usuario);
  }

}



