import { Component, OnInit } from '@angular/core';
import {ActivatedRoute} from '@angular/router';
import {ProductosadminService} from '../../../services/productosadmin/productosadmin.service';
import {Producto} from '../agregarproducto-admin/Productos';
import {Router} from '@angular/router';
import {CollectionsAdminService} from '../../../services/collectionsadmin/collections-admin.service';
@Component({
  selector: 'app-verproducto-especifico-admin',
  templateUrl: './verproducto-especifico-admin.component.html',
  styleUrls: ['./verproducto-especifico-admin.component.css']
})
export class VerproductoEspecificoAdminComponent implements OnInit {
idProducto:string;
statusProducto;

idProductoRecuperado:string;
producto={ 
  _id:"",
  idProducto:"",
    nombreProducto:"",
    requiereInventario:null,
    sku:"",
    descripcionProducto:"",
    imagenes:["","",""],
    existenciasIniciales:0,
    existenciasActuales:0,
    existenciasMinimas:0,
    imagenDescripcion:"",
    precioCompra:null,
    precioVenta:null,
    tienePromocion:null,
    nombrePromocion:"",
    cantidadPromocion:null,
    categorias:[""],
    tags:[""],
    proveedor:"",
    categoria:[""]
};
disabledInput=true;
imagenes={
  imagen1:"",
  imagen2:"",
  imagen3:""
};
  constructor(private route:ActivatedRoute, private http:ProductosadminService, 
    private router:Router , private httpCollections:CollectionsAdminService) { 
    this.route.params.subscribe(params=>{
    this.idProducto=params.id;

    });
  }

  ngOnInit() {
    this.obtenerDatos();
    this.obtenerColecciones();
  }

  obtenerDatos(){
    this.http.obtenerProductoEspecifico(this.idProducto).subscribe(producto=>{
     
        this.statusProducto=producto;
        this.producto=this.statusProducto.producto;
        this.idProductoRecuperado=this.statusProducto.producto._id;
        console.log(this.producto);
        this.arrayImagenes(this.producto.imagenes);
    });
  }

  arrayImagenes(array){
    this.imagenes={
      imagen1:array[0],
      imagen2:array[1],
      imagen3:array[2]
    }
  }

  editarProducto(){
    this.disabledInput=false;
  }

  guardarCambios(){
  this.producto.imagenes[0]=this.imagenes.imagen1;
  this.producto.imagenes[1]=this.imagenes.imagen2;
  this.producto.imagenes[2]=this.imagenes.imagen3; 
 

  
  this.http.editarProducto(this.producto).subscribe(producto=>{
   
    location.reload();

  });
  
  this.buscarProductos(this.coleccionesRecuperadas);
 
 }
  
 respuestaEliminar;
 eliminarProducto(id){
   this.http.eliminarProducto(id).subscribe(producto=>{
     console.log(producto)
    this.respuestaEliminar=producto;
    if(this.respuestaEliminar.success=true){
    this.router.navigate(["/productosadmin/ver"]);
    }
   })
 }

 objetoColecciones;
 coleccionesRecuperadas;
 obtenerColecciones(){
  this.httpCollections.obtenerColecciones().subscribe(colecciones=>{
  this.objetoColecciones=colecciones;
  this.coleccionesRecuperadas=this.objetoColecciones.colecciones;
  console.log(this.coleccionesRecuperadas);
  this.buscarProductos(this.coleccionesRecuperadas);
  });

 }

 buscarProductos(array){
   let objetoColeccion;
  for(let i=0;i<array.length;i++){
    objetoColeccion=array[i];
    console.log(objetoColeccion.productos);
    for(let a=0;a<objetoColeccion.productos.length;a++){
      if(this.idProducto==objetoColeccion.productos[a]._id){
        console.log(objetoColeccion.productos[a]);
        objetoColeccion.productos[a]=this.producto;
        console.log(objetoColeccion);
        this.httpCollections.editarColeccion(objetoColeccion._id, objetoColeccion).subscribe(coleccion => {
          console.log(coleccion);
        });
      }else {

        console.log('false');

      }
    }
  }
 }

  }




