import { Injectable } from '@angular/core';
import {Router,CanActivate} from "@angular/router";
import {HttpAdminService} from "../services/http-admin.service";
@Injectable()
export class GuardAdminService implements CanActivate{

  constructor(private http:HttpAdminService, private router:Router) { }
  
  canActivate(){
     if(this.http.loggedIn()){
        return true;
     } else{
       this.router.navigate(["/login/admin"]);
       return false;
     }
  }
}



