import { Component, OnInit } from '@angular/core';
import {ActivatedRoute} from "@angular/router";
import {PedidosService} from "../../../services/pedidosService/pedidos.service";
@Component({
  selector: 'app-venta-especifica',
  templateUrl: './venta-especifica.component.html',
  styleUrls: ['./venta-especifica.component.css']
})
export class VentaEspecificaComponent implements OnInit {

  constructor(private route:ActivatedRoute,private pedidosHttp:PedidosService) { }
   
  idPedido;
  ngOnInit() {
    this.route.params.subscribe(params=>{
      this.idPedido=params.id;
      
    });
    this.obtenerPedidoEspecifico(this.idPedido);
  }
  
   objetoPedido={
     _id:"",
     idCliente:"",
     cliente:{nombre:[],
      pedidos:[],
      telefono:"",
      email:""
      },
      envio:{
        nombreComp:"",
        numeroGuia:""
      },
      estadoPedido:"",
      direccion:{
        estado:"",
        ciudad:"",
        colonia:"",
        numero:"",
        calle:"",
        telefono:"",
        email:"",
        apellido:"",
        nombre:""
      },
      productos:[]
      
   }

   listaEstados=false;
   
   objetoRecuperado;
  obtenerPedidoEspecifico(id){
   this.pedidosHttp.obtenerPedidoEspecifico(id).subscribe(pedido=>{

     this.objetoRecuperado=pedido;
     this.objetoPedido=this.objetoRecuperado.pedido;
     console.log(this.objetoPedido);
        });
  }

  cambiarEstado(data){
  
    this.objetoPedido.estadoPedido=data.innerHTML;
  }

  guardarCambios(){
    this.pedidosHttp.editarPedido(this.objetoPedido._id, this.objetoPedido).subscribe(pedido=>{
     location.reload();
      
    });
  }

}



