import { Component, OnInit } from '@angular/core';
import {Producto} from "./Productos";
import {ProductosadminService} from "../../../services/productosadmin/productosadmin.service";
import {CategoriasadminService} from "../../../services/categoriasadmin/categoriasadmin.service";
import {AlertsService} from "../../../services/alertsService/alerts.service";
import {Router} from "@angular/router";
@Component({
  selector: 'app-agregarproducto-admin',
  templateUrl: './agregarproducto-admin.component.html',
  styleUrls: ['./agregarproducto-admin.component.css']
})
export class AgregarproductoAdminComponent implements OnInit {
  
  nuevoProducto={
    idProducto:"",
    nombreProducto:"",
    requiereInventario:false,
    sku:"",
    descripcionProducto:"",
    imagenes:["","",""],
    existenciasIniciales:null,
    existenciasActuales:null,
    existenciasMinimas:null,
    imagenDescripcion:"",
    precioCompra:null,
    precioVenta:null,
    tienePromocion:false,
    nombrePromocion:"",
    cantidadPromocion:null,
    categorias:[""],
    tags:[""],
    proveedor:""
  }

  alerts:any=[];

  arrayCategorias;

  

  constructor(private http:ProductosadminService,private httpCategorias:CategoriasadminService, private serviceAlerts:AlertsService, private router:Router) { }

  ngOnInit() {
    this.cargarCategorias();
    this.alerts=this.serviceAlerts.obtenerAlerta();
    this.serviceAlerts.resetArray();
  }

  cargarCategorias(){
    this.httpCategorias.obtenerCategorias().subscribe(categorias=>{
     this.arrayCategorias=categorias;
    });
  }

  guardarProducto(datos){
    console.log(datos.form.value);
    
   this.http.guardarProductoNuevo(datos.form.value).subscribe(producto =>{
        console.log(producto);
   });

   let objetoAlerta={
     type:'info',
     msg:'Producto creado',
     timeout:3000
   }
   this.serviceAlerts.agregarAlerta(objetoAlerta);
   this.router.navigate(["/productosadmin/agregar/redirect"]);



   
  } 
 

}



