import { Injectable } from '@angular/core';
import {BehaviorSubject} from "rxjs/BehaviorSubject";
@Injectable()
export class AlertsService {

  alerts=[];
  constructor() { }

  agregarAlerta(alerta){
    this.alerts=[];
    this.alerts.push({
      type:alerta.type,
      msg:alerta.msg,
      timeout:alerta.timeout
    });
    
  }
  resetArray(){
    this.alerts=[];
  }
  obtenerAlerta(){
    return this.alerts;
  }
}



