import { Component, OnInit } from '@angular/core';
import {Router} from "@angular/router";

@Component({
  selector: 'app-productosadmi',
  templateUrl: './productosadmi.component.html',
  styleUrls: ['./productosadmi.component.css']
})
export class ProductosadmiComponent implements OnInit {

openSideBar=false;


  constructor(private router:Router) {

   }
   agregarProducto(){
     this.router.navigate(["/productosadmin/agregar"]);
   }

  ngOnInit() {
    this.router.navigate(["/productosadmin/ver"]);
  }
  

}



