import { Component, OnInit } from '@angular/core';
import {PedidosService} from "../../../../services/pedidosService/pedidos.service";
import {ActivatedRoute} from "@angular/router";
@Component({
  selector: 'app-pedido-especifico',
  templateUrl: './pedido-especifico.component.html',
  styleUrls: ['./pedido-especifico.component.css']
})
export class PedidoEspecificoComponent implements OnInit {

  constructor(private pedidoHttp:PedidosService,private route:ActivatedRoute) { }

  idPedido:string="";
  ngOnInit() {
    this.route.params.subscribe(params=>{
     this.idPedido=params.id;
    });
    this.recuperarPedidoById(this.idPedido);
  }

  objetoRecuperado;
  pedidoRecuperado={total:0};
  totalVenta:number=0
  recuperarPedidoById(id){
   this.pedidoHttp.obtenerPedidoEspecifico(id).subscribe(pedido=>{
     console.log(pedido);
     this.objetoRecuperado=pedido;
      this.pedidoRecuperado=this.objetoRecuperado.pedido;
      console.log(this.pedidoRecuperado);
      this.totalVenta=this.pedidoRecuperado.total;
   });
  }

}



