import { Component, OnInit } from '@angular/core';
import {Router} from "@angular/router";
@Component({
  selector: 'app-sidebarproductos',
  templateUrl: './sidebarproductos.component.html',
  styleUrls: ['./sidebarproductos.component.css']
})
export class SidebarproductosComponent implements OnInit {
 openSideBar=false;
  constructor(private router:Router) { }

  ngOnInit() {
  }

  agregarProducto(){
    this.router.navigate(["/productosadmin/agregar"]);
  }
  listaProductos(){
    this.router.navigate(["/productosadmin/ver"]);
  }
  agregarCategorias(){
    this.router.navigate(["/productosadmin/categorias/agregar"])
  }
  verColecciones(){
    this.router.navigate(["/coleccionesadmin"]);
  }
  listaCategorias(){
    this.router.navigate(["/productosadmin/categorias"]);
  }

  openNav(sidebar){
    this.openSideBar=true;
  }
  closeNav(){
    this.openSideBar=false;
  }

}



