import {Pipe,PipeTransform} from "@angular/core";

@Pipe({name:"cantidadMultiplicacion"})
export class precioMultiplicacionPipe implements PipeTransform {
transform(value:number, cantidad: string):number{
 let mul=parseFloat(cantidad);
 return mul*value;
}

}


