  cantidadProductos=0;
  animacionesNav=[false,false,false,false,false];
  ngOnInit() {
    this.cargarNavbar();
    this.arrayProductos= this.carritoHttp.obtenerProductos();
   this.cantidadProductos=this.arrayProductos.length;
   this.verificarInicio();
  this.detectarBrowser();
   
  }
  scroll(evt){
    this.scrollY=evt.deltaY;
    if(this.scrollY<0){
      this.fixedTop=false;
    }else if(this.scrollY>0){
      this.fixedTop=true;
    }
  }

  objetoNavbar;
  navbarRecuperada;
  cargarNavbar(){
    this.httpNavbar.obtenerNavbar().subscribe(navbar=>{
     this.objetoNavbar=navbar;
     if(this.objetoNavbar.navbar.length>0){
       this.navbarRecuperada=this.objetoNavbar.navbar[0].navMain;
       this.asignarValores(this.navbarRecuperada);
     
     }
      
    });
  }

