import { Component, OnInit } from '@angular/core';
import {Admin} from "../register-admin/admin";
import {HttpAdminService} from "../../services/http-admin.service";
import {Router} from "@angular/router";
@Component({
  selector: 'app-login-admin',
  templateUrl: './login-admin.component.html',
  styleUrls: ['./login-admin.component.css']
})
export class LoginAdminComponent implements OnInit {
submitAdmin:Admin={
  nombre:"",
  mail:"",
  username:"",
  password:""
}
loginSuccess:boolean;
  constructor(private http:HttpAdminService, private router:Router) { }

  ngOnInit() {
  }
submitDatos(data){
  console.log(data.form.value);
  let objetoUser;
   this.http.validarCuenta(data.form.value).subscribe(user=>{
     objetoUser=user;
   if(objetoUser.success==false){
    console.log("User not found");
    this.loginSuccess=false;
   }else if(objetoUser.success==true){
     this.http.storeUserData(objetoUser.token,objetoUser.admin);
     this.router.navigate(["/admin/ventas"]);
     this.loginSuccess=true;
   }
 });
}
}



