import { Component, OnInit } from '@angular/core';
import {CollectionsAdminService} from "../../../services/collectionsadmin/collections-admin.service";
import {ActivatedRoute} from "@angular/router";
import {CarritoComprasService} from "../../../services/carritoService/carrito-compras.service";
import {Router} from "@angular/router";
import {AlertsService} from "../../../services/alertsService/alerts.service";
@Component({
  selector: 'app-coleccion-especifica-front',
  templateUrl: './coleccion-especifica-front.component.html',
  styleUrls: ['./coleccion-especifica-front.component.css']
})
export class ColeccionEspecificaFrontComponent implements OnInit {

  alerts:any=[];
 idProducto:string="";
  constructor(private httpCollections:CollectionsAdminService, private route:ActivatedRoute, private carritoService:CarritoComprasService, private router:Router
  ,private alertsService:AlertsService) {

    this.route.params.subscribe(params=>{
      this.idProducto=params.id;
    });
    setTimeout(this.obtenerColeccionById(this.idProducto),500);
    this.alerts=this.alertsService.obtenerAlerta();
  this.resetArray();
   }
  
  ngOnInit() {
    
  }

  resetArray(){
    this.alertsService.resetArray();
  }

  objetoRecuperado;
  coleccionRecuperada;
  
  arrayProductos=[];
  obtenerColeccionById(idProducto){
    this.httpCollections.obtenerColeccionEspecifica(idProducto).subscribe(collection=>{
     this.objetoRecuperado=collection;
   this.coleccionRecuperada=this.objetoRecuperado.coleccion;    
   console.log(this.coleccionRecuperada);
      for(let i=0;i<this.coleccionRecuperada.productos.length;i++){
        this.compararProductosCarrito(this.coleccionRecuperada.productos[i]._id);
          this.arrayProductos.push(this.coleccionRecuperada.productos[i]);
          
      }
      this.filtrarCategorias(this.coleccionRecuperada);
      this.filtrarMarcas(this.coleccionRecuperada);
    });
    
  }

  arregloCategorias=[];
  filtrarCategorias(coleccion){
   for(let i=0;i< coleccion.productos.length;i++){
     if(!this.arregloCategorias.includes(coleccion.productos[i].categoria[0])){
     this.arregloCategorias.push(coleccion.productos[i].categoria[0]);
     }
   }
   console.log(this.arregloCategorias);
  }

  arregloMarcas=[];
  filtrarMarcas(marcas){
    for(let i=0;i<marcas.productos.length;i++){
      if(!this.arregloMarcas.includes(marcas.productos[i].proveedor)){
        this.arregloMarcas.push(marcas.productos[i].proveedor);
      }
    }

  }

  filtrarProductosMarcas(marca){
   this.arrayProductos=[];
    for(let i=0;i<this.coleccionRecuperada.productos.length;i++){
      if(marca==this.coleccionRecuperada.productos[i].proveedor){
             this.arrayProductos.push(this.coleccionRecuperada.productos[i]);
      }
    }
  }

  filtrarProductos(nombre){
    this.arrayProductos=[];
    for(let i=0;i<this.coleccionRecuperada.productos.length;i++){
      if(nombre==this.coleccionRecuperada.productos[i].categoria[0]){
             this.arrayProductos.push(this.coleccionRecuperada.productos[i]);
      }else if(nombre=="todos"){
            this.arrayProductos=this.coleccionRecuperada.productos;
      }
    }
  }
  


  arrayProductosCarrito=[];
  compararProductosCarrito(idProductos){
    this.arrayProductosCarrito=this.carritoService.obtenerProductos();
    for(let i=0;i<this.arrayProductosCarrito.length;i++){
      
      if(idProductos==this.arrayProductosCarrito[i]._id){
       
     
      this.cambiarPropiedadAdded(idProductos);
      }
    }
    

  }

  cambiarPropiedadAdded(idProductos){
    this.coleccionRecuperada.productos;
        for(let i=0;i<this.coleccionRecuperada.productos.length;i++){
      if(this.coleccionRecuperada.productos[i]._id==idProductos){
        this.coleccionRecuperada.productos[i].added=true;
        }
    }

  }

  agregarCarrito(producto){
    this.carritoService.agregarProducto(producto);
    const alertObj={
      type:'success',
      msg:"Producto agregado al carrito",
      timeout:3000
    };
     this.alertsService.agregarAlerta(alertObj);
    this.router.navigate(["/carrito/coleccion/"+this.idProducto]);
  }

  verProducto(id){
    this.router.navigate(["/producto/"+id]);
  }

  verCarrito(){
    this.router.navigate(["/carrito"]);
  }
}



