import { Component, OnInit } from '@angular/core';
import {UsuariosService} from "../../../services/userService/usuarios.service";
import {ActivatedRoute} from "@angular/router";
import {PedidosService} from "../../../services/pedidosService/pedidos.service";

@Component({
  selector: 'app-perfil-usuario',
  templateUrl: './perfil-usuario.component.html',
  styleUrls: ['./perfil-usuario.component.css']
})
export class PerfilUsuarioComponent implements OnInit {

  constructor(private httpUser:UsuariosService, private route:ActivatedRoute, private pedidosHttp:PedidosService) { }

  idUsuario;
  ngOnInit() {
    this.route.params.subscribe(params=>{
      this.idUsuario=params.id;
    });
    this.obtenerDatosUsuario(this.idUsuario);
    this.obtenerPedidos();
 }

 objetoUser;
 userRecuperado={
   _id:"",
   email:"",
   nombre:[],
   password:"",
   pedidos:[],
   direcciones:[],
   telefono:""
 };
 obtenerDatosUsuario(id){
   this.httpUser.obtenerUsuarioById(id).subscribe(user=>{
     this.objetoUser=user;
     this.userRecuperado=this.objetoUser.user;
     console.log(this.userRecuperado);
   });
 }

 objetoPedidos;
 pedidoRecuperado=[];
 numeroPedidos:number=0;

 
 obtenerPedidos(){
   this.pedidosHttp.obtenerPedidoByUser(this.idUsuario).subscribe(pedidos=>{
     this.objetoPedidos=pedidos;
      this.pedidoRecuperado=this.objetoPedidos.pedido;
      this.numeroPedidos=this.pedidoRecuperado.length;
     console.log(this.pedidoRecuperado);
   })
 }

}



