import { Injectable } from '@angular/core';
import {HttpClient} from "@angular/common/http";
import {Producto} from "../../components/productos-admin/agregarproducto-admin/Productos";
@Injectable()
export class ProductosadminService {
 domain:string="http://localhost:8080"
  constructor(public http:HttpClient) { }

  guardarProductoNuevo(producto){
    return this.http.post(`/productos`,producto);
  }

  obtenerProductos(){
    return this.http.get(`/productos`);
  }

  obtenerProductoEspecifico(id){

    return this.http.get(`/productos/`+id)
  }
  buscarProducto(nombre){
    return this.http.post(`/productos/name`,nombre);
  }
  editarProducto(producto){
    return this.http.put(`/producto`,producto);
  }
  eliminarProducto(id){
    return this.http.delete(`/producto/delete/`+id);
  }
  obtenerProductoMarca(marca){
    return this.http.post(`/producto/find/`+marca,marca);
  }

}



