import { Injectable } from '@angular/core';
import {Coleccion} from "../../components/productos-admin/colecciones/coleccion";
import {HttpClient} from "@angular/common/http";
@Injectable()
export class CollectionsAdminService {
domain:string="http://localhost:8080"
  constructor(public http:HttpClient) { }

 guardarColeccion(coleccion){
   return this.http.post(`/colecciones`,coleccion);
 }

 obtenerColecciones(){
   return this.http.get(`/colecciones`);
 }

 obtenerColeccionEspecifica(id){
   return this.http.get(`/colecciones/`+id);
 }

 editarColeccion(id,coleccion){
   return this.http.post(`/colecciones/`+id,coleccion);
 }
buscarColeccionPorNombre(nombre){
  return this.http.post(`/colecciones/buscar/nombre`,nombre);
}

eliminarColeccion(id){
  return this.http.delete(`/colecciones/`+id);
}


}



