import { Component, OnInit } from '@angular/core';
import {ProductosadminService} from "../../../services/productosadmin/productosadmin.service"
import {ActivatedRoute} from "@angular/router";
import {CarritoComprasService} from "../../../services/carritoService/carrito-compras.service"
import {Router} from "@angular/router";
@Component({
  selector: 'app-producto-especifico',
  templateUrl: './producto-especifico.component.html',
  styleUrls: ['./producto-especifico.component.css']
})
export class ProductoEspecificoComponent implements OnInit {

  constructor(private httpProductos:ProductosadminService, private route:ActivatedRoute, private carritoService:CarritoComprasService,private router:Router) { }
idProducto:"";
  ngOnInit() {
   this.route.params.subscribe(params=>{
        this.idProducto=params.id;
        console.log(this.idProducto);
   });
   this.buscarProductoEspecifico(this.idProducto);
   

  }

  objetoRecuperadoProducto;
  productoRecuperado={
    _id:"",
    added:false,
    idProducto:"",
    nombreProducto:"",
    requiereInventario:null,
    sku:"",
    descripcionProducto:"",
    imagenes:["","",""],
    existenciasIniciales:0,
    existenciasActuales:0,
    existenciasMinimas:0,
    imagenDescripcion:"",
    precioCompra:null,
    precioVenta:null,
    tienePromocion:null,
    nombrePromocion:"",
    cantidadPromocion:null,
    categorias:[""],
    tags:[""],
    proveedor:""
  };
  buscarProductoEspecifico(id){
    this.httpProductos.obtenerProductoEspecifico(id).subscribe(producto=>{

      this.objetoRecuperadoProducto=producto;
      this.productoRecuperado=this.objetoRecuperadoProducto.producto;
      this.compararProductosCarrito(id);
    
      this.buscarProductoMarca(this.productoRecuperado.proveedor);

    });
    
  }

  agregarCarrito(producto){
    this.carritoService.agregarProducto(producto);
    this.router.navigate(["/carrito/producto/"+this.idProducto]);
  }

  arrayProductosCarrito=[];
  compararProductosCarrito(idProductos){
    this.arrayProductosCarrito=this.carritoService.obtenerProductos();
    for(let i=0;i<this.arrayProductosCarrito.length;i++){
      
      if(idProductos==this.arrayProductosCarrito[i]._id){
       
     
      this.cambiarPropiedadAdded(idProductos);
      }
    }
   
    
  }
  verCarrito(){
    this.router.navigate(["/carrito"]);
  }

   
  cambiarPropiedadAdded(idProductos){
      if(this.productoRecuperado._id==idProductos){
        this.productoRecuperado.added=true;
        }
  }
  
  objetoRelacionados;
  arrayRelacionados=[];
  buscarProductoMarca(marca){
      this.httpProductos.obtenerProductoMarca(marca).subscribe(productos=>{
        this.objetoRelacionados=productos;
        this.asignarValoresRelacionados(this.objetoRelacionados.producto);
      });
  }

  asignarValoresRelacionados(arrayProductos){
    for(let i=0;i<arrayProductos.length;i++){
      if(this.productoRecuperado.nombreProducto!=arrayProductos[i].nombreProducto){
        if(this.arrayRelacionados.length<=4){
          this.arrayRelacionados.push(arrayProductos[i]);
          console.log(this.arrayRelacionados);
        }
      }
    }
  }




}



