import { Component, OnInit } from '@angular/core';
import {HttpAdminService} from "../../services/http-admin.service";
import {Router} from  "@angular/router";

@Component({
  selector: 'app-navbar',
  templateUrl: './navbar.component.html',
  styleUrls: ['./navbar.component.css']
})
export class NavbarComponent implements OnInit {

  constructor(private http:HttpAdminService, private router:Router) { }

  cargarProductos(){
    this.router.navigate(["/productosadmin"]);
  }

  ngOnInit() {

   
  }
  cerrarSesion(){
    this.http.cerrarSesion();
  }
  interfazTienda(){
    this.router.navigate(["/uiadmin"]);
  }

  cargarVentas(){
    this.router.navigate(["/admin/ventas"]);
  }

}



