import { Component, OnInit } from '@angular/core';
import {ActivatedRoute} from "@angular/router";
import {Router} from "@angular/router";
@Component({
  selector: 'app-redirect-coleccion-especifica',
  templateUrl: './redirect-coleccion-especifica.component.html',
  styleUrls: ['./redirect-coleccion-especifica.component.css']
})
export class RedirectColeccionEspecificaComponent implements OnInit {

  constructor(private route:ActivatedRoute,private router:Router) { }
idProducto:string="";
  ngOnInit() {
    this.route.params.subscribe(params=>{
      this.idProducto=params.id;
    });
    this.redirectColecciones(this.idProducto);
  }

  redirectColecciones(id){
   this.router.navigate(["/colecciones/front/"+id]);

  }

}



