import { Component, OnInit } from '@angular/core';
import {UsuariosService}from "../../../../services/userService/usuarios.service";
import {ActivatedRoute} from "@angular/router";
import {Router} from "@angular/router";
@Component({
  selector: 'app-direcciones',
  templateUrl: './direcciones.component.html',
  styleUrls: ['./direcciones.component.css']
})
export class DireccionesComponent implements OnInit {

  objetoUser;
 userRecuperado={
   _id:"",
   email:"",
   nombre:[],
   password:"",
   pedidos:[],
   direcciones:[],
   telefono:""
 };

 objetoDireccion={
   nombre:"",
   apellido:"",
   email:"",
   telefono:"",
   calle:"",
   numero:"",
   colonia:"",
   ciudad:"",
   estado:""
 }

 arregloDirecciones=[];
  constructor(private userHttp:UsuariosService, private route:ActivatedRoute, private router:Router) { }
   tieneDirecciones:boolean=false;
  idUsuario:string="";
  ngOnInit() {
    this.route.params.subscribe(params=>{
       this.idUsuario=params.id;
    
    });
    this.obtenerDatosUsuario(this.idUsuario);
  }


  obtenerDatosUsuario(id){
    this.userHttp.obtenerUsuarioById(id).subscribe(usuario=>{
        this.objetoUser=usuario;
        this.userRecuperado=this.objetoUser.user;
        this.objetoDireccion=this.userRecuperado.direcciones[0];
    });
  }

  guardarDireccion(data){
  this.arregloDirecciones=[];
    this.arregloDirecciones.push(this.objetoDireccion);
    this.userRecuperado.direcciones=this.arregloDirecciones;
    this.userHttp.editarUsuario(this.userRecuperado).subscribe(usuario=>{
      
    });
    this.router.navigate(["/redirect/direccion/user/"+this.idUsuario]);
  }

}



