import { Component, OnInit } from '@angular/core';
import {ControlInterfazService} from "../../../services/controlInterfaz/control-interfaz.service";
import {Interfaz} from "../../control-interfaz/Interfaz";
import {CollectionsAdminService} from "../../../services/collectionsadmin/collections-admin.service";
import {Coleccion} from "../../productos-admin/colecciones/coleccion";
import {CarritoComprasService} from "../../../services/carritoService/carrito-compras.service";
import {Router} from "@angular/router";
import {Subject} from 'rxjs/Subject';
import {debounceTime} from 'rxjs/operator/debounceTime';
import {AlertsService} from "../../../services/alertsService/alerts.service";
@Component({
  selector: 'app-destacados-index',
  templateUrl: './destacados-index.component.html',
  styleUrls: ['./destacados-index.component.css']
})
export class DestacadosIndexComponent implements OnInit {
 
  alerts:any=[];


  interfazRecuperada;
  objetoInterfaz={
    _id:"",
    coleccion1:"",
    coleccion2:"",
    coleccion3:"",
    imagenesCarousel:["","",""],
    isEditable:true
  };
  coleccionesRecuperadas=false;
  
   producto={
    idProducto:"464654654654",
    nombreProducto:"productoOBSERVABLE",
    requiereInventario:false,
    sku:"",
    descripcionProducto:"",
    imagenes:["","",""],
    existenciasIniciales:null,
    existenciasActuales:null,
    existenciasMinimas:null,
    imagenDescripcion:"",
    precioCompra:null,
    precioVenta:null,
    tienePromocion:false,
    nombrePromocion:"",
    cantidadPromocion:null,
    categorias:[""],
    tags:[""],
    proveedor:""
   }

  constructor(private httpInterfaz:ControlInterfazService, private httpColections:CollectionsAdminService, 
    private carritoService:CarritoComprasService , private router:Router,private alertsService:AlertsService) { }

    
  ngOnInit() {
    

  this.recuperarInterfaz();
  this.alerts=this.alertsService.obtenerAlerta();
  this.resetArray();
  
  }

  resetArray(){
    this.alertsService.resetArray();
  }
      
  
  agregarCarrito(producto){
    const alertObj={
      type:'success',
      msg:"Producto agregado al carrito",
      timeout:3000
    };
     this.alertsService.agregarAlerta(alertObj);
    this.carritoService.agregarProducto(producto);
    this.router.navigate(["/carrito/destacados/add"]);
  }

 recuperarInterfaz(){
   this.httpInterfaz.obtenerInterfaz().subscribe(interfaz=>{

   this.interfazRecuperada=interfaz;

   this.objetoInterfaz=this.interfazRecuperada.interfaz[0];
  
   
     console.log(this.objetoInterfaz);
     /*
    this.asignarDatos(this.objetoInterfaz.colecciones);
  */
   
   this.recuperarColeccion1(this.objetoInterfaz.coleccion1);
   this.recuperarColeccion2(this.objetoInterfaz.coleccion2);
   this.recuperarColeccion3(this.objetoInterfaz.coleccion3);

   });
 }

 recuperarColeccion1(id){
  let coleccionRecuperada;
  this.httpColections.obtenerColeccionEspecifica(id).subscribe(coleccion1=>{
   coleccionRecuperada=coleccion1;
   this.coleccionSeccion1=coleccionRecuperada.coleccion;
   for(let a=0;a<this.coleccionSeccion1.productos.length;a++){
    //console.log(this.coleccionRecuperada.productos[a]._id);
    
    this.productosColeccion.push(this.coleccionSeccion1.productos[a]);
    this.compararProductosCarrito(this.coleccionSeccion1.productos[a]._id);
  }
  
  
  });
}
recuperarColeccion2(id){
  let coleccionRecuperada;
  this.httpColections.obtenerColeccionEspecifica(id).subscribe(coleccion1=>{
   coleccionRecuperada=coleccion1;
   this.coleccionSeccion2=coleccionRecuperada.coleccion;
   for(let a=0;a<this.coleccionSeccion2.productos.length;a++){
    //console.log(this.coleccionRecuperada.productos[a]._id);
    this.productosColeccion.push(this.coleccionSeccion2.productos[a]);
    this.compararProductosCarrito(this.coleccionSeccion2.productos[a]._id);
  }
  
   console.log(this.coleccionSeccion2);
  });
}
recuperarColeccion3(id){
  let coleccionRecuperada;
  this.httpColections.obtenerColeccionEspecifica(id).subscribe(coleccion1=>{
   coleccionRecuperada=coleccion1;
   this.coleccionSeccion3=coleccionRecuperada.coleccion;
   for(let a=0;a<this.coleccionSeccion3.productos.length;a++){
    //console.log(this.coleccionRecuperada.productos[a]._id);

    this.productosColeccion.push(this.coleccionSeccion3.productos[a]);
    this.compararProductosCarrito(this.coleccionSeccion3.productos[a]._id);
  }
   console.log(this.productosColeccion);
  });
}

 arrayProductosCarrito=[];
  compararProductosCarrito(idProductos){
    this.arrayProductosCarrito=this.carritoService.obtenerProductos();
    for(let i=0;i<this.arrayProductosCarrito.length;i++){
      
      if(idProductos==this.arrayProductosCarrito[i]._id){
       
     
      this.cambiarPropiedadAdded(idProductos);
      }
    }
   
    
  }

  cambiarPropiedadAdded(idProductos){
    
        for(let i=0;i<this.productosColeccion.length;i++){
      if(this.productosColeccion[i]._id==idProductos){
        this.productosColeccion[i].added=true;
        }
    }

  }



 primerColeccion={
  _id:"",
  nombre:"",
  descripcion:"",
  productos:[]
};

coleccionSeccion1={
  _id:"",
  nombre:"",
  productos:[]
}
coleccionSeccion2={
  _id:"",
  nombre:"",
  productos:[]
}
coleccionSeccion3={
  _id:"",
  nombre:"",
  productos:[]
}
productosColeccion=[];

objetoColeccionRecuperada;
coleccionRecuperada;
 verProducto(id){
   this.router.navigate(["/producto/"+id]);
 }

 verCarrito(){
   this.router.navigate(["/carrito"]);
   
 }



}



